<template>
	<view class="wrap">
		<view class="top"></view>
		<view class="content">
			<view class="title">欢迎登入图书商城</view>
			<view class=" u-m-b-40">
				<input class="u-border-bottom" v-model="email" placeholder="请输入邮箱" confirm-type="next" @confirm="nextFocus"/>
				<view class="tips" v-show="isShowTip">请输入正确的邮箱号</view>
			</view>
			<input class="u-border-bottom u-m-b-40" type="password" v-model="password" placeholder="请输入密码" :focus="isFocus" confirm-type="done" @confirm="submit"/>
			<button :loading="submitBtn" :disabled="submitBtn" @click="submit" :style="[inputStyle]" class="getCaptcha">登录</button>
			<view class="alternative">
				<view class="issue" @tap="jump('pages/auth/register')">注册</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			email: '',
			password:'',
			isShowTip:false,
			submitBtn:false,
			// 密码框聚焦
			isFocus:false
		}
	},
	onLoad({email,password}) {
		this.email=email
		this.password=password
	},
	computed: {
		inputStyle() {
			let style = {};
			if(this.email){
				this.isShowTip=true
			}
			if(this.$u.test.email(this.email)){
				this.isShowTip=false
			}
			if(this.$u.test.email(this.email) && this.password) {
				style.color = "#fff";
				style.backgroundColor = this.$u.color['primary'];
			}
			return style;
		}
	},
	methods: {
		async submit(){
			if(!this.$u.test.email(this.email) || !this.password)  return //如果邮箱和密码校验不通过就retrun
			const res = await this.$u.api.login({email:this.email,password:this.password})
			this.$u.vuex('vuex_token',res.access_token)
			const userInfo = await this.$u.api.userInfo()
			this.$u.vuex('vuex_user',userInfo)
		} 
	}
};
</script>

<style lang="scss" scoped>
.wrap {
	font-size: 28rpx;
	overflow: hidden;
	.content {
		width: 600rpx;
		margin: 80rpx auto 0;
		.title {
			text-align: left;
			font-size: 60rpx;
			font-weight: 500;
			margin-bottom: 100rpx;
		}
		input {
			text-align: left;
			margin-bottom: 10rpx;
			padding-bottom: 6rpx;
		}
		.tips {
			font-size: 20rpx;
			color: $u-type-error;
			margin-top: 4rpx;
		}
		.getCaptcha {
			background-color: #a2b8ff;
			color: $u-tips-color;
			border: none;
			font-size: 30rpx;
			padding: 12rpx 0;
			
			&::after {
				border: none;
			}
		}
		.alternative {
			color: $u-tips-color;
			display: flex;
			justify-content: space-between;
			margin-top: 30rpx;
		}
	}
	.buttom {
		.loginType {
			display: flex;
			padding: 350rpx 150rpx 0 150rpx;
			justify-content:space-between;
			.item {
				display: flex;
				flex-direction: column;
				align-items: center;
				color: $u-content-color;
				font-size: 28rpx;
			}
		}
	}
}
</style>